.body-div {
    text-align: center;
    margin: 0;
    padding: 0;
    font-family: 'Open Sans', sans-serif;
}
#header {
    background-color: #f1f1f1;
    border-bottom: 1px solid #ddd;
    padding: 30px;
    text-align: center;
    color: #666;
    font-size: 30px;
}
#content {
    padding: 20px;
    text-align: center;
    font-size: 18px;
}
#content a {
    color: #3e68ff;
}
#getstarted {
    border-radius: 10px;
    font-size: 18px;
    max-width: 800px;
    margin: 10px auto 20px auto;
    padding: 10px;
    color: #444;
}
.section {
    margin-bottom: 40px;
    padding: 0 20px;
}
h2 {
    font-size: 20px;
    font-weight: normal;
}
h2 a {
    text-decoration: none;
}
.section p {
    font-size: 14px;
}
#footer {
    padding: 20px;
    text-align: center;
    font-size: 12px;
    color: #888;
    max-width: 540px;
    margin-left: auto;
    margin-right: auto;
}
#footer a {
    color: #555;
}